export default {
	data() {
		return {
			info: {},
			items: [],
			parts: [],
			imgs: []
		}
	},
	onLoad(option) {
		var info = JSON.parse(decodeURIComponent(option.para));
		this.info = info;
		global.$http.post('/car/repair/repairInfo', {
			params: {
				order_code: this.info.order_code
			},
		}).then(res => {
			if (res.status === "0") {
				res.data.items.forEach(c => {
					this.items.push({
						item_name: c.item_name,
						item_fee: c.item_fee,
						remark: c.remark
					});
				});
				res.data.parts.forEach(c => {
					this.parts.push({
						parts_name: c.parts_name,
						num: c.num,
						price: c.price
					});
				});
				
				res.data.imgs.forEach(c => {
					this.imgs.push(c);
				});
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	computed:{
		itemsPrice: function() {
			var price = 0;
			this.items.forEach(c=>{
				price += c.item_fee;
			});
			return price;
		},
		partsPrice: function() {
			var price = 0;
			this.parts.forEach(c=>{
				price += c.price * c.num;
			});
			return price;
		}
	},
	methods: {
		
	}
}
